import generatorlogo from './password-generator.png'

const Generatop=() =>{
return (
  <div className="card" style={{ width: "18rem" }}>
    <img src={generatorlogo} className="card-img-top" alt="reinstagram" id="reinstagram" />
    <div className="card-body">
      <h4 className="card-title">Password Generator</h4>
      <p className="card-text">This app is for generate a secured password for user.<br /> Technology: Html, Javascript, Css</p>
      <a href="https://sinkyraj.github.io/PasswordGenerator/" className="btn btn-primary" target="_blank">Link to Project</a>
      <p></p>
      <a href="https://github.com/sinkyraj/PasswordGenerator" className="btn btn-primary" target="_blank">Link to GitHub</a>
    </div>
  </div>
)


}

export default Generatop