import google from './emp_directory.png'

const Employeetrack = () => {
  return (
    <div className="card" style={{ width: "18rem" }}>
      <img style={{ height: "15rem" }} src={google} className="card-img-top" alt="reinstagram" id="reinstagram" />
      <div className="card-body">
        <h4 className="card-title">Employee Directory</h4>
        <p className="card-text">By this app user can view and access entire employee information at once.<br /> <br /> Technology: React, Express.js, API, Node.js, MVC Framework</p>
        <a href="https://sinkyraj.github.io/employee-directory/" className="btn btn-primary" target="_blank">Link to Project</a>
        <p></p>
        <a href="https://github.com/sinkyraj/employee-directory" className="btn btn-primary" target="_blank">Link to GitHub</a>
      </div>
    </div>

  )



}

export default Employeetrack