import fitness from './fitness-tracker.png'

const Fitness = () => {
  return (
    <div className="card" style={{ width: "18rem" }}>
      <img src={fitness} className="card-img-top" alt="reinstagram" id="reinstagram" />
      <div className="card-body">
        <h4 className="card-title">Fitness Tracker</h4>
        <p className="card-text">By this app user will be able to view, create and track daily workouts.<br /> <br /> Technology: HTML, JavaScript, CSS, Express.js, Mongoose, MVC</p>
        <a href="https://still-ocean-27336.herokuapp.com/?id=606d2b6b6b91f40015605a68" className="btn btn-primary" target="_blank">Link to Project</a>
        <p></p>
        <a href="https://github.com/sinkyraj/Workout_Tracker" className="btn btn-primary" target="_blank">Link to GitHub</a>
      </div>
    </div>

)



}

export default Fitness