import google from './googlebb.jpg'

const ggoole = () => {
return(
  <div className="card" style={{ width: "18rem" }}>
    <img src={google} className="card-img-top" alt="reinstagram" id="reinstagram" />
    <div className="card-body">
      <h4 className="card-title">Google Book Search</h4>
      <p className="card-text">This app is about using Google Books API display books based on user searches and save the books.<br /><br /> Technology: React, JavaScript,  API, Materialize, Express.js, Mongoose, Node.js, MVC</p>
      <a href="https://googlebook-shweta.herokuapp.com/" className="btn btn-primary" target="_blank">Link to Project</a>
      <p></p>
      <a href="https://github.com/sinkyraj/google-book-search" className="btn btn-primary" target="_blank">Link to GitHub</a>
    </div>
  </div>

)



}

export default ggoole