import weatherLogo from './dash.jpg'


const weathergram = () => {
  return (
    <div className="card" style={{ width: "18rem" }}>
      <img src={weatherLogo} className="card-img-top" alt="reinstagram" id="reinstagram" />
      <div className="card-body">
        <h4 className="card-title">Weather Dashboard</h4>
        <p className="card-text">This app shows weather forecast (temperature, humidity, wind speed,  UV index) for the cities.<br /><br />
          
       Technology: HTML, JavaScript, Jquery, CSS, Bootstrap, API </p>
        <a href="https://sinkyraj.github.io/WeatherDashboard/" className="btn btn-primary" target="_blank">Link to Project</a>
        <p></p>
        <a href="https://github.com/sinkyraj/WeatherDashboard" className="btn btn-primary" target="_blank">Link to GitHub</a>
      </div>
    </div>
  )
}

export default weathergram